import { useTranslation } from 'react-i18next'
import { useEffect } from 'react'

import './App.css'
import Layout from './layout/Layout'
import env from './env'

function App () {
  const { t } = useTranslation()
  const title = env.title
  useEffect(() => {
    document.title = t(title)
  }, [t])

  return (
    <div className='App'>
      <Layout />
    </div>
  )
}

export default App
