import { Button } from 'antd'
import { useSelector, useDispatch } from 'react-redux'
import { setLogin, setLogout } from '../store/slices/loginSlice'

const loginOrLogout = () => {
  const { isLogin } = useSelector((state) => state.login)
  const dispatch = useDispatch()

  const login = () => {
    dispatch(setLogin())
  }

  const logout = () => {
    dispatch(setLogout())
  }

  const buttonText = isLogin ? 'logout' : 'login'

  const buttonTarget = isLogin ? logout : login

  return (
    <Button type='primary' onClick={buttonTarget}>{buttonText}</Button>
  )
}

export default loginOrLogout
