import { useEffect } from 'react'
import { Menu } from 'antd'
import { useHistory } from 'react-router-dom'
import { useSelector } from 'react-redux'

import * as router from '../router'

const { SubMenu } = Menu

const menu = () => {
  const history = useHistory()
  const { isLogin } = useSelector((state) => state.login)

  useEffect(() => {
    if (!router.paths(isLogin).includes(history.location.pathname) && history.location.pathname !== '/') {
      history.goBack()
    }
  }, [isLogin])

  const handleClick = e => {
    history.push(e.key)
  }

  return (
    <Menu onClick={handleClick} mode='inline'>
      {router.menu(isLogin).map(item => {
        return item.paths.length === 1
          ? (
            <Menu.Item key={item.paths[0].path}>{item.paths[0].name}</Menu.Item>
            )
          : (
            <SubMenu key={item.name} title={item.name}>
              {item.paths.map(subItem => {
                return (
                  <Menu.Item key={subItem.path}>{subItem.name}</Menu.Item>
                )
              })}
            </SubMenu>
            )
      })}
    </Menu>
  )
}

export default menu
