import { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { useTranslation } from 'react-i18next'
import { Layout, Row, Col, Select } from 'antd'

import LoginOrLogout from '../components/AuthButton'

import env from '../env'
const { Header } = Layout
const { Option } = Select

const header = () => {
  const style = {
    color: 'white'
  }

  const { level, token } = useSelector((state) => state.login)

  const { i18n } = useTranslation()

  const [language, setLanguage] = useState(i18n.language)

  useEffect(() => {
    i18n.changeLanguage(language)
  }, [language])

  const languageChangedHandler = value => {
    setLanguage(value)
  }

  const languageList = []

  Object.entries(env.locale).forEach(([key, value]) => {
    languageList.push((
      <Option key={key} value={key}>{value}</Option>
    ))
  })

  return (
    <Header>
      <Row justify='space-between'>
        <Col span={8}><div style={style}>{`${level} ${token}`}</div></Col>
        <Col span={8}>
          <Select defaultValue={language} style={{ width: 80 }} onChange={languageChangedHandler}>
            {languageList}
          </Select>
          <LoginOrLogout />
        </Col>
      </Row>
    </Header>
  )
}

export default header
