import { configureStore, combineReducers } from '@reduxjs/toolkit'
import loginSlice from './slices/loginSlice'
import storage from 'redux-persist/lib/storage/session'
import { persistReducer } from 'redux-persist'
import thunk from 'redux-thunk' // * 要導入redux-persist需要引用redux-thunk來做包裝，不然redux本身會報錯
/**
 * TODO 研究 redux-thunk 和 redux middleware 實際用處是什麼
 * https://medium.com/@WendellLiu/redux-middleware%E5%A4%A7%E7%95%A5%E6%9E%B6%E6%A7%8B-ace7e646c929#.l7syseaiu
 * https://medium.com/frochu/%E9%80%81%E8%AE%93%E4%BD%A0%E7%9A%84action%E8%83%BD%E4%BD%9C%E6%9B%B4%E5%A4%9A-redux-thunk-c07bc5488e48
 */
const persistConfig = {
  key: 'root',
  storage
}

const reducers = combineReducers({
  login: loginSlice
})

const persistedReducer = persistReducer(persistConfig, reducers)

export default configureStore({
  reducer: persistedReducer,
  middleware: [thunk]
})
