import { Layout } from 'antd'
import Menu from './Menu'
import Content from './Content'
import Header from './Header'

const { Footer, Sider } = Layout

const layout = () => (
  <Layout>
    <Header />
    <Layout>
      <Sider>
        <Menu />
      </Sider>
      <Content />
    </Layout>
    <Footer>Footer</Footer>
  </Layout>
)

export default layout
