import i18n from 'i18next'
import { initReactI18next } from 'react-i18next'
import languageDetector from './detector'

import tw from './tw.json'
import cn from './cn.json'

const resources = {
  tw: {
    translation: tw
  },
  cn: {
    translation: cn
  }
}

i18n.use(initReactI18next).use(languageDetector).init({
  resources,
  // lng: 'tw', // 預設語言 //!要啟動 languageDetector 的lookup功能，不能事先設定預設語言
  fallbackLng: 'tw', // 如果當前切換的語言沒有對應的翻譯則使用這個語言，
  detection: {
    order: ['detector'], // languageDetector客製化功能抓取位置
    caches: ['sessionStorage'] // languageDetector語言儲存位置
  },
  interpolation: {
    escapeValue: false // 如果語系檔中有巢狀的結構，則escapeValue要設為false，這樣就可以透過物件的方式來取得巢狀內的翻譯
  }
})

export default i18n
