import { lazy } from 'react'

const menuList = [
  {
    name: 'index',
    paths: [
      { id: '1', name: 'Option1', path: '/option1', content: lazy(() => import('../contents/Option1')) }
    ]
  },
  {
    name: 'info',
    paths: [
      { id: '2', name: 'Option2', path: '/option2', content: lazy(() => import('../contents/Option2')) },
      { id: '3', name: 'Option3', path: '/option3', content: lazy(() => import('../contents/Option3')) }
    ]
  },
  {
    name: 'testLogin',
    paths: [
      { id: '4', name: 'Login-Option1', path: '/login-option1', content: lazy(() => import('../contents/LoginOption1')) },
      { id: '5', name: 'Login-Option2', path: '/login-option2', content: lazy(() => import('../contents/LoginOption2')) }
    ]
  }
]

const permissionIdList = ['4', '5']

const authFilter = (isLogin) => {
  return menuList.map(menuItem => {
    const menuTemp = { ...menuItem }
    menuTemp.paths = menuItem.paths.filter(pathItem => {
      return isLogin ? true : !permissionIdList.includes(pathItem.id)
    })
    return menuTemp
  }).filter(item => item.paths.length)
}

export const menu = isLogin => { return authFilter(isLogin) }

export const contentList = isLogin => { return authFilter(isLogin).map(menuItem => menuItem.paths).flat().map(pathItem => ({ path: pathItem.path, content: pathItem.content })) }

export const paths = isLogin => { return authFilter(isLogin).map(menuItem => menuItem.paths).flat().map(pathItem => pathItem.path) }
