import LanguageDetector from 'i18next-browser-languagedetector'

const detector = {
  name: 'detector',
  lookup (options) {
    // options -> are passed in options
    if (sessionStorage.getItem('i18nextLng')) return sessionStorage.getItem('i18nextLng') // * sessionStorage 有已經儲存的語言，以sessionStorage優先
    const languageType = window.navigator.language.split('-')
    if (languageType[0] === 'zh') return languageType[1].toLowerCase() === 'tw' ? 'tw' : 'cn' // * 不是繁中，就全部轉簡中
    return languageType[0].toLowerCase() // * 不是中文的話，返回語言大分類
  }
}

const languageDetector = new LanguageDetector()
languageDetector.addDetector(detector)

export default languageDetector
